body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Comment {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-justify-content: center;
            justify-content: center;
    border-radius: 5px;
    background-color: #f7f7f7;
    margin: 5px;
    padding: 5px;
}

.Comment .votes {
    color: #3b5998;
    font-size: 0.8rem;
    font-weight: bold;

}

.Comment .comment-menu {
    min-width: 350px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: flex-end;
            align-items: flex-end;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
}

.Comment button {
    background: inherit;
    color: #3b5998;
    border: none;
    cursor: pointer;
    font-size: 1rem;
}

.Comment button:hover {
    color: #dfe3ee;
}
.CommentsList {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-justify-content: center;
            justify-content: center;
    border-top: 1px solid grey;
    border-bottom: 1px solid grey;
    padding: 5px;
    list-style: none;
    list-style-position: inherit;
}
.Form {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

textarea {
    min-width: 350px;
    font-size: 1rem;
}

.Form button {
    margin: 2px;
    background: inherit;
    color: #3b5998;
    border: 1px solid;
    cursor: pointer;
    font-size: 1rem;
    border-radius: 5px;
}

.Form button:hover {
    color: #fff;
    background: #3b5998;
}
.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.App h1 {
  color: #282c34;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

